@keyframes expand {
  0% {
    transform: scale(.6);
  }
  80% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes flyInLeft{
  0% {
    opacity: 0;
            transform: translateX(-100rem);
  }

  80% {
            transform: translateX(.5rem);
  }

  100% {
    opacity: 1;
            transform: translate(0);
  }
}
